<template>
  <div>
    <div class="v-modal" v-if="show">
      <div class="v-mask" @click="clickMask"></div>
      <div class="v-modal-wrap">
        <div class="v-modal-body">
          <p class="v-modal-title">{{title}}</p>
          <p>{{text}}</p>
        </div>
        <div class="v-modal-footer">
          <a class="v-btn fail" @click="clickFail"></a>
          <a class="v-btn confirm" @click="clickConfirm"></a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'v-dialog',
  props: {
    // 
    title: {
      type: String,
      default: ''
    },
    // 
    text: {
      type: String
    },
    // 
    show: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    // 
    clickMask () {
      this.$emit('mask-click')
    },
    // 
    clickFail () {
      this.$emit('fail-click')
    },
    // 
    clickConfirm () {
      this.$emit('confirm-click')
    },
  }
}
</script>

<style lang="less" scoped>
  @import './index.less';
</style>